import MoviesDB from '../data/MoviesDB';
import urlParse from '../routes/urlParse';

class dataRender {
  constructor({
    container, title, layouts, totalData, data, pagination, paginationName, link, slug,
  }) {
    this._container = container;
    this._title = title || null;
    this._layouts = layouts || 'flex';
    this._totalData = totalData;
    this._data = data;
    this._pagination = pagination || false;
    this._paginationName = paginationName;
    this._link = link || false;
    this._slug = slug || null;
  }

  dataPagination = async (page) => {
    const url = urlParse.parseUrlWithoutCombiner();
    const setPosision = this._container.getBoundingClientRect().top + window.pageYOffset - 85;
    window.scrollTo({
      top: setPosision,
      behavior: 'smooth',
    });

    this._container.numberPage = page;

    switch (this._paginationName) {
      case 'now-playing':
        this._container.data = await MoviesDB.MoviesNowPlaying(page);
        break;
      case 'upcomming':
        this._container.data = await MoviesDB.Upcomming(page);
        break;
      case 'trending':
        this._container.data = await MoviesDB.Trending(page);
        break;
      case 'top-rated-movies':
        this._container.data = await MoviesDB.TopRated(page);
        break;
      case 'top-rated-tv':
        this._container.data = await MoviesDB.TopRated(page, 'tv');
        break;
      case 'popular-tv':
        this._container.data = await MoviesDB.PopularMovieOrTv(page, 'tv');
        break;
      case 'genre-list':
        this._container.data = await MoviesDB.MovieOrTvByGenre(url.id, page);
        break;
      case 'on-air-tv':
        this._container.data = await MoviesDB.TvOnAir(page);
        break;
      default:
        break;
    }
  };

  paginations = () => {
    let page = 1;

    const next = () => {
      page++;
      this.dataPagination(page);
    };

    const prev = () => {
      page--;
      this.dataPagination(page);
    };

    this._container.next = next;
    this._container.previous = prev;
  };

  async render() {
    try {
      const data = await this._data;
      // console.log(data.length);
      if (data.length === 0) {
        return;
      }
      this._container.title = this._title;
      this._container.layout = this._layouts;
      this._container.pagination = this._pagination;
      this._container.link = this._link;
      this._container.slug = this._slug;
      this._container.totalData = this._totalData;
      this._container.data = data;
      if (this._pagination) {
        this._container.numberPage = 1;
        this.paginations();
      }
    } catch (error) {
      console.log(error);
    }
  }
}

export default dataRender;
